<template>
    <div class="hot-recommends">
        <div class="title">热销推荐</div>
        <ul>
            <li class="item" v-for="item of list" :key="item.id">
                <img :src="item.imgUrl" alt="">
                <div class="item-info">
                    <p class="item-title">{{item.title}}</p>
                    <p class="item-intro">{{item.desc}}</p>
                    <button class="item-button">查看详情</button>
                </div>
            </li>
            
        </ul>
    </div>
</template>

<script>
export default {
    name:'HomeRecommonds',
    props:{
        list:Array
    },
    data(){
        return {

        }
    }
}
</script>

<style lang="stylus" scoped>
    .title
        height .8rem
        line-height .8rem
        background #eee
        padding-left .16rem
    .item        
        overflow hidden
        padding .16rem
        border-bottom 1px solid #eaeaea
        img
            width 1.7rem
            height 1.7rem
            float left
        .item-info
            overflow hidden
            padding-left .266667rem
            .item-title
                line-height: 0.54rem
                font-size: 0.32rem
                overflow: hidden
                white-space: nowrap
                text-overflow: ellipsis
            .item-intro
                line-height: 0.6rem;
                color: #ccc;
                overflow: hidden
                white-space: nowrap
                text-overflow: ellipsis
            .item-button
                margin-top .16rem
                line-height: 0.4rem;
                background #ff9300
                color #ffffff
                padding 0 .08rem
</style>


