<template>
    <div class="weekend">
        <div class="title">周末去哪儿</div>
        <ul>
            <li v-for="item in list" :key="item.id">
                <img :src="item.imgUrl" alt="">
                <div class="item-info">
                    <p class="item-title">{{item.title}}</p>
                    <p class="item-desc">{{item.desc}}</p>
                </div>
            </li>
            
        </ul>
    </div>
</template>

<script>
export default {
    name:'HomeWeekends',
    props:{
        list:Array
    }
}
</script>

<style lang="stylus" scoped>
    .weekend
        .title
            height .8rem
            line-height .8rem
            background #eee
            padding-left .16rem
        img 
            width 100%
            border 0
        .item-info
            .item-title
                padding .213333rem 0rem
                font-size .32rem
            .item-desc
                padding-bottom .213333rem
                color #ccc
                
</style>


