<template>
    <div class="wrapper">
        <!-- 头部组件 -->
        <home-header></home-header>

        <!-- 轮播图组件 -->
        <home-swiper :list="swiperList"></home-swiper>

        <!-- 图标轮播图 -->
        <home-icon :list="iconList"></home-icon>

        <!-- 热销推荐 -->
        <home-recommonds :list="recommendList"></home-recommonds>

        <!-- 周末去哪儿 -->
        <home-weekends :list="weekendList"></home-weekends>
    </div>
</template>

<script>
import axios from 'axios'
import HomeHeader from './components/Header'
import HomeSwiper from './components/Swiper'
import HomeIcon from './components/Icon'
import HomeRecommonds from './components/Recommond'
import HomeWeekends from './components/Weekend'


export default {
    name:'Home',
    components:{
        HomeHeader,
        HomeSwiper,
        HomeIcon,
        HomeRecommonds,
        HomeWeekends
    },
    data(){
        return {
            swiperList:[],
            iconList:[],
            recommendList:[],    //热销推荐
            weekendList:[]       //周末去哪儿
        }
    },
    mounted(){
        this.getIndexInfo();
    },
    methods:{
        getIndexInfo(){
            axios.get("/static/api/index.json").then((response) => {
                console.log(response.data)
                this.swiperList = response.data.data.swiperList
                this.iconList = response.data.data.iconList
                this.recommendList = response.data.data.recommendList
                this.weekendList = response.data.data.weekendList
            })
        }
    }
}
</script>

<style lang="stylus" scoped>
    
</style>


