<template>
    <div class="list" ref="list">
        <div class="content">
            <div class="area">
                <div class="title">当前城市</div>
                <div class="button-list">
                    <div class="button-wrapper">
                        <div class="button">北京</div>
                    </div>
                </div>
            </div>
            <div class="area">
                <div class="title">热门城市</div>
                <div class="button-list">
                    <div class="button-wrapper" v-for="item of hotCities" :key="item.id">
                        <div class="button">{{item.name}}</div>
                    </div>
                    
                </div>
            </div>

            <div class="area" v-for="(item,key) of cities" :key="item.id">
                <div class="title">{{key}}</div>
                <div class="item-list">
                    <div class="item" v-for="(value,index) of item" :key="index">
                        {{value.name}}
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
    name:'CityList',
    props:{
        cities:Object,
        hotCities:Array
    },
    mounted(){
        var wrapper = this.$refs.list
        this.scroll = new BScroll(wrapper)
    }
}
</script>

<style lang="stylus" scoped>
    .list
        overflow: hidden;
        position: absolute;
        top: 1.58rem;
        left: 0;
        right: 0;
        bottom: 0;

    .area
        .title
            height .64rem
            line-height .64rem
            background #eee
            padding-left .16rem
        .button-list 
            overflow hidden
            .button-wrapper
                box-sizing border-box               
                width 33.33%
                float left                
                .button 
                    margin .16rem
                    border: 0.02rem solid #ccc;
                    border-radius: 0.06rem;
                    padding .133333rem
                    text-align center
        .item-list
            .item
                line-height .64rem
                padding-left .266667rem
                border-bottom: 1px solid #eaeaea
</style>


