<template>
    <div class="iconlist">
        <mt-swipe :auto="0">
            <mt-swipe-item v-for="(item) of pages" :key="item.id">
                <div class="icon" v-for="icon of item" :key="icon.id">
                    <a href="">
                        <img :src="icon.imgUrl" alt="">
                    </a>
                    <p class="desc">
                        {{icon.desc}}
                    </p>
                </div>
                
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>

<script>
import Vue from 'vue'
import { Swipe, SwipeItem } from 'mint-ui';

Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
export default {
    name:'HomeIcon',
    props:{
        list:Array
    },
    computed:{
        pages () {
            const pages = []
            this.list.forEach((item, index) => {
                const page = Math.floor(index / 8)  // 0 - 7 ---> 0  8-15----> 1
                if (!pages[page]) {
                    pages[page] = []
                }
                pages[page].push(item)
            })
            return pages
        }
    },
    data(){
        return {
            item:{
                imgUrl:'http://img1.qunarzz.com/piao/fusion/1611/54/ace00878a52d9702.png'
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
    .iconlist
        height 4.8rem
        .icon
            box-sizing border-box
            width 25%
            float left
            text-align center
            padding 0.28rem
            img 
                width 80%
            .desc
                color: #333;
                padding:.133333rem
                overflow: hidden
                white-space: nowrap
                text-overflow: ellipsis

</style>



